import './styles.css'

// *************** Instructor's method ***************

import { useState } from "react";
import ImageList from "./ImageList";
import ImageForm from "./ImageForm";


const INITIAL_DATA = [
   {
      url:"https://media-cldnry.s-nbcnews.com/image/upload/t_fit-1240w,f_auto,q_auto:best/rockcms/2022-08/220805-border-collie-play-mn-1100-82d2f1.jpg",
      caption: "Dog",
   }

]

function ImageBoard() {
   const [images, setImages] = useState(INITIAL_DATA);

   const addImage =(image) => {
      setImages([...images, image])
   }
   return (
      <div>
         <ImageForm  addImage={addImage}/>
         
         <ImageList images={images} />
      </div>
   );
}

export default ImageBoard;
